      );
    },
    openTelegram() {
      window.open("https://t.me/blucaOffiCommu");
    },
    countdown() {
      var countdown = setInterval(() => {
        let dueDate = new Date("Sept 15, 2021 20:00:00").getTime();
        let now = new Date().getTime();
        let distance = dueDate - now;
        if (distance < 0) {
          distance = 0;
          clearInterval(countdown);
        }
        this.setTime(distance);
      }, 1000);
    },
    prependZero(number) {
      if (number <= 9) return "0" + number;
      else return number;
    },
    setTime(distance) {
      this.timer.days = Math.floor(distance / (1000 * 60 * 60 * 24));
      this.timer.hours = this.prependZero(
        Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
      );
      this.timer.minutes = this.prependZero(
        Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
      );
      this.timer.seconds = this.prependZero(
        Math.floor((distance % (1000 * 60)) / 1000)
      );
    },
